<div id="browse-main"><EntityTable :tableData="tableData" title="" v-slot="{ entities }" v-if="tableData.length"> <div class="table-container"><div class="column label">Dataset ID</div><div class="column label">Name</div><div class="column label">Description</div><div class="column label">Size</div><template v-for="(row, idx) in entities"><div class="column"> <router-link :to="{ name: &quot;search&quot;, query: {dataset_ids: row[&quot;Dataset ID&quot;], limit: 100}}">{{row["Dataset ID"]}}</router-link></div><div class="column">{{row.Name}}</div><div class="column">{{row.Description?.length > 75 ? row.Description.substr(0,75)+"..." : row.Description}}</div><div class="column">{{row.Size}}</div></template></div></EntityTable><div class="loading" v-else><LoadingSpinner></LoadingSpinner></div></div>