<div class="table-main"><div class="table-container"><div class="header"><div class="title-holder"><div class="title">{{ title }}</div></div></div><div class="search-area" v-if="displaySearch"><label for="search">Search: </label><input type="text" id="search" v-model="searchString"></div><div class="content"><div class="entities-holder"><slot :entities="paginatedEntities"></slot></div><div class="pagination" v-if="pagination &amp;&amp; entities"><div class="select">Showing <select name="pagination" id="pagination" v-model="pagination"><option value="10">10</option><option value="25">25</option><option value="50">50</option><option value="100">100</option></select> of {{filteredEntities.length}} entries.</div><div class="paginav first" @click="currentPage = 1"><img class="chevron" src="/img/arrowL.png"><img class="chevron" src="/img/arrowL.png"><span class="word">First</span></div><div class="prev paginav" @click="currentPage = pagiPrev"> <img class="chevron" src="/img/arrowL.png"><span class="word">Previous</span></div><div class="paginav" @click="currentPage = currentPage &gt; 1 ? currentPage - 1 : currentPage" :class="currentPage &gt; 1 ? &quot;&quot; : &quot;no-click&quot;"><span class="word">{{ currentPage > 1 ? currentPage - 1 : "..."}}</span></div><div class="paginav no-click"><span class="word">{{ currentPage }}</span></div><div class="paginav" @click="currentPage = currentPage &lt; lastPage ? currentPage + 1 : currentPage" :class="currentPage &lt; lastPage ? &quot;&quot; : &quot;no-click&quot;"><span class="word">{{ currentPage < lastPage ? currentPage + 1 : "..."}}</span></div><div class="next paginav" @click="currentPage = pagiNext"> <span class="word">Next</span><img class="chevron" src="/img/arrowR.png"></div><div class="paginav last" @click="currentPage = lastPage"><span class="word">Last</span><img class="chevron" src="/img/arrowR.png"><img class="chevron" src="/img/arrowR.png"></div></div></div></div></div>